.seo-hero {
  padding-top: 247px;
  padding-bottom: 120px;
  background: url("../img/seo-bg.webp") no-repeat center/ cover,
    url("../img/seo-bg.jpg") no-repeat center/ cover, var(--dark-color);
  position: relative;
  z-index: 1;

  @media (max-width: 480px) {
    padding-top: 178px;
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    box-shadow: inset 1px 1px 232px #000000;
    z-index: 2;
  }

  &__content {
    @media (max-width: 480px) {
      max-width: 408px;
    }
    position: relative;
    z-index: 3;
    &-title {
      font-style: normal;
      font-weight: 400;
      font-size: 45px;
      line-height: 56px;
      color: var(--light-color);
    }

    &-subtitle {
      margin-top: 39px;
      max-width: 816px;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 29px;
      color: var(--light-color);
      & span {
        color: var(--primary-accent-color);
      }

      @media (max-width: 575px) {
        font-size: 20px;
      }
    }

    &-cta {
      margin-top: 101px;
      display: flex;
      align-items: center;
      gap: 30px;

      @media (max-width: 480px) {
        margin-top: 40px;
        gap: 10px;
      }

      @media (max-width: 460px) {
        flex-direction: column;
        align-items: start;
      }
    }

    &-btn {
      @media (max-width: 480px) {
        padding: 14px;
        gap: 12px;
        flex-grow: 1;
        text-align: center;
        min-width: 180px;
      }
    }
  }
}
